<template>
	<div class="pd-10 jh-pannel">
		<div class="mg-12">
				<chooseSchool @handleChangeUnit="handleChangeUnit"></chooseSchool>
		</div>
		<div>
			<div class="tree"><tree-table ref="recTree" :list.sync="treeDataSource" @handlerExpand="handlerExpand" @handlerChooseModel="handlerChooseModel"></tree-table></div>
			<div class="form">
				<div v-if="!!curDept.parentId"><el-button size="mini" type="primary" class="mg-12" @click="handleAddService">添加服务事项</el-button></div>
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column fixed="left" label="操作" width="180">
						<template slot-scope="scope">
							<el-button type="text" size="small" @click="handleOperate('edit', scope.row)">编辑</el-button>
							<el-button type="text" size="small" @click="handleOperate('ssjh', scope.row)">编辑实施计划</el-button>
							<el-button type="text" size="small" @click="handleOperate('check', scope.row)">查看</el-button>
						</template>
					</el-table-column>
					<el-table-column prop="date" label="服务对象" width="180"></el-table-column>
					<el-table-column prop="name" label="服务事项" width="180"></el-table-column>
					<el-table-column prop="address" label="服务类别" width="180"></el-table-column>
					<el-table-column prop="date" label="服务指南" width="180"></el-table-column>
					<el-table-column prop="name" label="信息化支持" width="180"></el-table-column>
					<el-table-column prop="address" label="建设状态" width="180"></el-table-column>
					<el-table-column prop="date" label="上线时间" width="180"></el-table-column>
					<el-table-column prop="name" label="承建公司" width="180"></el-table-column>
				</el-table>
				 <el-pagination
					@current-change="handleCurrentChange"
					:current-page.sync="currentPage"
					:page-size="pageSize"
					layout="total, prev, pager, next"
					:total="records">
				</el-pagination>
			</div>
		</div>
		
		
		<jbxxDailog :show.sync="jbxxShow" @handleCommitService="handleCommitService"></jbxxDailog>
		<ssjhDailog :show.sync="ssjhShow"></ssjhDailog>
	</div>
</template>

<script>
import treeTable from '@/components/tree/tree-table.vue';
import jbxxDailog from '@/views/BusinessPage/schoolServices/jbxx-dialog.vue';
import ssjhDailog from '@/views/BusinessPage/schoolServices/ssjh-dialog.vue';
import chooseSchool from '@/components/BusinessPage/chooseSchool.vue';
export default {
	data() {
		return {
			jbxxShow: false,
			ssjhShow:false,
			treeDataSource: [],
			currentPage:1,
			pageSize:15,
			records:0,
			tableData: [],
			unit:{}, //单位
			curDept:{} //当前部门
		};
	},
	methods: {
		
		handleChangeUnit(params){
			this.curDept = {};
			this.unit = params;
			this.treeDept();
			this.pageServiceItem();
		},
		// 提交服务
		handleCommitService(params){
			let formData = params;
			formData.fwdx = params.fwdx.join(',');
			formData.bmbh = this.curDept.id;
			formData.bmmc = this.curDept.title;
			
			this.$post(this.API.saveServiceItem,formData).then(res=>{
				if(res.state == 'success'){
					this.$message({message:'添加成功',type:'success'});
					this.pageServiceItem();
				}else{
					this.$message({message:res.msg,type:'error'});
				}
			})
		},
		
		// 分页
		handleCurrentChange(data){
			this.currentPage = data;
			this.pageServiceItem();
		},
		// 展开
		handlerExpand(m) {
			m.isExpand = !m.isExpand;
		},
		// 选中
		handlerChooseModel(m){
			this.curDept = m;
		},
		// 添加服务事项
		handleAddService(){
			this.jbxxShow = true;
		},
    // 编辑，查看，编辑计划
		handleOperate(type, params) {
			if(type == 'edit'){
				this.jbxxShow = true;
			}else if(type == 'ssjh'){
				this.ssjhShow = true;
			}
		},
		// 获取服务事项列表
		pageServiceItem(){
			this.$get(this.API.pageServiceItem,{
				 curPage:this.currentPage,
				 pageSize:this.pageSize,
				 dwbh:this.unit.dwbh
			}).then(res=>{
				if(res.state == 'success'){
					console.log(res);
					if(!res.data || !res.data.rows ){
						this.tableData = []
					}else{
						this.tableData = res.data.rows
					}
					this.records = res.data.records;
				}else{
					
				}
			})
		},
		// 获取树
		treeDept(){
			this.$get(this.API.treeDeptWithUnit,{
				dwbh:this.unit.dwbh,
				dwmc:this.unit.dwmc,
				zts:'1,3'
			}).then(res=>{
				if(res.state == 'success'){
					this.treeDataSource = res.data;
				}
			})
		}
	},
	components: {
		treeTable,
		jbxxDailog,
		ssjhDailog,
		chooseSchool
	}
};
</script>

<style>
.jh-pannel {
	min-width: 1300px;
	max-width: 90%;
	margin: 0 auto;
}
.jh-pannel:after {
	content: '';
	display: block;
	clear: both;
}
.tree {
	width: 300px;
	float: left;
}
.form {
	width: calc(100% - 320px);
	padding: 10px;
	background: #fff;
	float: right;
}
</style>



